*{margin:0;padding:0;-webkit-box-sizing: border-box;box-sizing: border-box;
}
li{list-style: none;
}
a{text-decoration: none;
}
html,body{height:100%;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;  
  font-size: .3rem;
  background: #f3f2f2;color:#353535;height:100%;position: relative;overflow-x: hidden;
}
button{border: 0;outline: 0;border-radius: 6px;
}
input{border: 0;outline: 0;font-size: .3rem;
}
/*位置*/
.pr{position: relative;
}
.pa{position: absolute;
}
/* 元素类型 */
.clear{clear:both;
}
.text-right{text-align: right;
}
.text-center{text-align: center;
}
.text-left{text-align: left;
}
.db{display: block;
}
.di{display: inline;
}
.dib{display: inline-block;
}
/* 变黑白 */
.bw{-webkit-filter: grayscale(100%);filter: grayscale(100%);
}
/*flex布局*/
.flex{display: -webkit-box;display: -ms-flexbox;display: flex;display: -webkit-flex;
}
/*水平轴是主轴 垂直轴是交叉轴*/
/*单根主轴方向*/
.fd-r{-webkit-box-orient: horizontal;-webkit-box-direction: normal;-ms-flex-direction: row;flex-direction: row;
}
.fd-rr{-webkit-box-orient: horizontal;-webkit-box-direction: reverse;-ms-flex-direction: row-reverse;flex-direction: row-reverse;
}
.fd-c{-webkit-box-orient: vertical;-webkit-box-direction: normal;-ms-flex-direction: column;flex-direction: column;
}
.fd-cr{-webkit-box-orient: vertical;-webkit-box-direction: reverse;-ms-flex-direction: column-reverse;flex-direction: column-reverse;
}
/*单根换行*/
.fw-n{-ms-flex-wrap: nowrap;flex-wrap: nowrap;
}
.fw-w{-ms-flex-wrap: wrap;flex-wrap: wrap;
}
.fw-wr{-ms-flex-wrap: wrap-reverse;flex-wrap: wrap-reverse;
}
/*单根主轴对齐方式*/
.jc-fs{-webkit-box-pack: start;-ms-flex-pack: start;justify-content: flex-start;
}
.jc-fe{-webkit-box-pack: end;-ms-flex-pack: end;justify-content: flex-end;
}
.jc-c{-webkit-box-pack: center;-ms-flex-pack: center;justify-content: center;
}
.jc-sb{-webkit-box-pack: justify;-ms-flex-pack: justify;justify-content: space-between;
}
.jc-fs{-ms-flex-pack: distribute;justify-content: space-around;
}
/*单根交叉轴对齐方式*/
.ai-fs{-webkit-box-align: start;-ms-flex-align: start;align-items: flex-start;
}
.ai-fe{-webkit-box-align: end;-ms-flex-align: end;align-items: flex-end;
}
.ai-c{-webkit-box-align: center;-ms-flex-align: center;align-items: center;
}
.ai-b{-webkit-box-align: baseline;-ms-flex-align: baseline;align-items: baseline;
}
.ai-s{-webkit-box-align: stretch;-ms-flex-align: stretch;align-items: stretch;
}
/*多根交叉轴对齐方式*/
/*父层配合fd-r,fw-w。子层配合width:100%。使用*/
.ac-fs{-ms-flex-line-pack: start;align-content: flex-start;
}
.ac-fe{-ms-flex-line-pack: end;align-content: flex-end;
}
.ac-c{-ms-flex-line-pack: center;align-content: center;
}
.ac-sb{-ms-flex-line-pack: justify;align-content: space-between;
}
.ac-sa{-ms-flex-line-pack: distribute;align-content: space-around;
}
.ac-s{-ms-flex-line-pack: stretch;align-content: stretch;
}
/*item属性*/
/*项目宽高分配比例*/
.fg-1{-webkit-box-flex: 1;-ms-flex-positive: 1;flex-grow: 1;
}
